@font-face {
    font-family: 'Source Sans Pro';
    font-style: normal;
    font-weight: 300;
    src: local('Source Sans Pro Light'), local('SourceSansPro-Light'),
    url(SourceSansPro-Light.woff2) format('woff2'),
    url(SourceSansPro-Light.woff) format('woff');
}

